<template>
    <v-container>
        <v-row>
            <!-- Kalkula�ka -->
            <v-col cols="12" md="6">
                <v-card>
                    <v-card-title>
                        <v-text-field v-model="display"
                                      readonly
                                      outlined
                                      dense
                                      class="text-right"
                                      variant="outlined"
                                      label="Display"
                                      hide-details />
                    </v-card-title>
                    <v-card-subtitle>
                        <v-row>
                            <v-col v-for="btn in buttons" :key="btn" cols="3">
                                <v-btn @click="appendToDisplay(btn )"
                                       block
                                       :color="btn.color">
                                    {{ btn.text }}
                                </v-btn>
                            </v-col>
                        </v-row>
                       
                        <v-btn @click="clearDisplay"
                               block
                               color="red">
                            C
                        </v-btn>
                    </v-card-subtitle>
                </v-card>
            </v-col>

            <!-- Historie -->
            <v-col cols="12" md="6">
                <v-card>
                    <v-card-title>History</v-card-title>
                    <v-data-table  
                                  :items="history"
                                   :headers ="headers"
                                  dense
                                  hide-default-footer
                                  class="elevation-1">
                        
                    </v-data-table>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    import axios from '../plugins/axios'
     export default {
        name: 'CalculatorPage',
        data() {
            return {
                display: '',
                history: [],
                input1: '',
                input2: '',
                operation: null,
                resultcalculated: false,
                result: null,
                buttons: [
                    { text: '7', color: 'primary',  type:'number'},
                    { text: '8', color: 'primary', type: 'number' },
                    { text: '9', color: 'primary', type: 'number' },
                    { text: '/', color: 'secondary', type: 'operation' },
                    { text: '4', color: 'primary', type: 'number' },
                    { text: '5', color: 'primary', type: 'number' },
                    { text: '6', color: 'primary', type: 'number' },
                    { text: '*', color: 'secondary', type: 'operation' },
                    { text: '1', color: 'primary', type: 'number' },
                    { text: '2', color: 'primary', type: 'number' },
                    { text: '3', color: 'primary', type: 'number' },
                    { text: '-', color: 'secondary', type: 'operation' },
                    { text: '0', color: 'primary', type: 'number' },
                    { text: '.', color: 'primary', type: 'number' },
                    { text: '=', color: 'secondary', type: 'result' },
                    { text: '+', color: 'secondary', type: 'operation' },
                ],
                headers: [

{ text: 'Kalkulace', value: 'calculation' },
{ text: 'Operation', value: 'operation' },
                    
                    ]
            };
        },
        computed: {
            //formattedHistory() {
            //    return this.history.map(op => ({ operation: op }));
            //}
        },
         
        methods: {
            addFunc() {
                this.result = parseFloat(this.input1) + parseFloat(this.input2);
            },
            divideFunc() {
                this.result = parseFloat(this.input1) / parseFloat(this.input2);
            },
            multipleFunc() {
                this.result = parseFloat(this.input1) * parseFloat(this.input2);
            },
            minusFunc() {
                this.result = parseFloat(this.input1) - parseFloat(this.input2);
            },

            appendToDisplay(value) {
                if (this.resultcalculated) {
                    //this.input1 = '';
                    //this.input2 = '';
                    //this.operation= null,
                    //this.result= null,
                    //    this.display = '';
                    //this.resultcalculated = false;
                    this.clearDisplay();

                }


                if (value.type === 'result') {
                    //this.display = result;
                    this.resultcalculated = true;
                     this.calculateResult();
                } else if (value.type === 'operation') {
                    this.operation = value.text;
                    this.display += value.text.toString();
                 }
                  else   {
                    if (this.operation != null && this.display.indexOf(this.operation) > 0) {
                        this.input2 += value.text;
                    } else {
                     this.input1 += value.text;
                    }
                    this.display += value.text.toString();
                }

            },
            getHistory() {
                axios.get('/home',).then(response => {
                    console.log(response)
                    this.history = response.data;
                }).catch(error => {
                    console.error('Chyba p�i odes�l�n� po�adavku:', error);
                });
            },


            clearDisplay() {
                this.input1 = '';
                this.input2 = '';
                this.operation = null,
                    this.result = null,
                    this.display = '';
                this.resultcalculated = false;            },
            calculateResult() {
                try {
                    // Use `Function` constructor for better safety instead of `eval`
                    //const result = new Function('return ' + this.display)();
                    switch (this.operation) {
                        case '*': this.multipleFunc(); break;
                        case '/': this.divideFunc(); break;
                        case '+': this.addFunc(); break;
                        case '-': this.minusFunc(); break;
                        default: break;
                    }
                    this.display = this.result;
                    // this.history.push(`${this.input1} ${this.operation} ${this.input2} = ${this.result}`);
                    this.resultcalculated = true;
                    //this.display += this.result;
                    // Send result to server
                    // P�iprav JSON po�adavek
                    let message = {message:this.display};
                 axios.post('/home/Error', message);
                    let request = {
                        calculation: `${this.input1} ${this.operation} ${this.input2} = ${this.result}`,
                        input1: this.input1.toString() ,
                        input2: this.input2.toString() ,
                        operation: this.operation.toString() ,
                        result: this.result.toString()
                    };
                    // Odesl�n� po�adavku na server (zabalen� v objektu `request`)
                    axios.post('/home', request).then(response => {
                        console.log('Data odesl�na na server:', response.data);
                        this.getHistory();
                    }).catch(error => {
                        console.error('Chyba p�i odes�l�n� po�adavku:', error);
                         axios.post('/home/Error', error);
                    });

                } catch (e) {
                    this.display = 'Error';
                }
            }
        },
        mounted() {
            // This will run when the component is mounted
            this.getHistory(); // Fetch the history when the component is mounted
        }
    };
</script>

<style scoped>
    .v-card {
        margin: 16px;
    }

    .v-text-field {
        font-size: 2em;
    }

    .v-btn {
        margin: 4px;
    }
</style>
